import React from "react";
import photo from "../images/ClarkPhoto.jpg"; // How get image to load

const About = () => (
  <div className="container bg-primary">
    <div className="headerSection text-center">
      <h1>Brittany J. Clark</h1>
    </div>
    <img
      src={photo}
      alt="headshot"
      className="rounded float-left border border-danger mr-3"
      style={({ width: "400px" }, { height: "300px" })}
    ></img>
    <p>
      My name is Brittany Clark and I live in Raleigh, NC. I currently work as a
      Client Success Manager for a software company based out of Cary, NC. Since
      college I have dabbled in programming languages taking courses in
      Javascript, SQL, html, and Python. After obtaining Masters degrees from
      the University of North Carolina at Chapel Hill in Social Work and Public
      Administration, I decided to return to complete the Full-stack Coding
      Bootcamp certificate program and graduated in April 2020. The program
      covered languages that include HTML5, CSS, JavaScript, jQuery, Bootstrap,
      Express.js, React.js, Node.js, Database Theory, MongoDB, MySQL, Command
      Line, Git, and more. I enjoy working in the full stack aspects and
      ultimately strive to create quality user interface that help people track
      and organize their data.{" "}
    </p>
  </div>
);

export default About;
